/* ---------------burger */
.burger {
  display: none;
  position: relative;
  width: 30px;
  height: 20px;
  z-index: 2; }
  @media (max-width: 1023px) {
    .burger {
      display: block; } }
  .burger span {
    position: absolute;
    top: 8px;
    left: 0;
    width: 100%;
    height: 4px;
    background-color: #FDB604;
    border-radius: 10px;
    transition: all .7s ease 0s;
    z-index: 2; }
  .burger::before, .burger::after {
    content: '';
    position: absolute;
    height: 4px;
    width: 100%;
    background-color: #000000;
    border-radius: 10px;
    transition: all .5s ease 0s;
    z-index: 1; }
  .burger::before {
    top: 0;
    width: 50%;
    left: 0; }
  .burger::after {
    bottom: 0;
    width: 50%;
    right: 0; }
  .active .burger span {
    transform: rotate(-45deg);
    top: 9px; }
  .active .burger::before, .active .burger::after {
    transform: rotate(45deg);
    background-color: #efefef; }
  .active .burger::before {
    top: 4px;
    left: 3px; }
  .active .burger::after {
    bottom: 2px;
    right: 2px; }

